#app2{
    border: 1px solid blue;
}
#app2 .tab-bar {
    display: flex;
}
#app2 .tab-bar li{
    border: 3px solid black;
    flex-grow: 1;
}
#app2 .tab-bar li.selected{
    background-color: aqua;
}

#app2 .tab-content li{
    display: none;
}
#app2 .tab-content li.active{
  display: block;
}

